.container {
  background-color: #fff;
  padding: 2rem;
  box-shadow: 1px 10px 18px rgba(34, 34, 34, 0.9);
}
.container .nav {
  background-color: #b8bac5;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  margin: 0 -3rem 2rem;
  box-shadow: 2px 10px 10px rgba(32, 32, 32, 0.9);
  position: relative;
}
.container .nav:before,
.container .nav:after {
  content: '';
  height: 0;
  width: 0;
  position: absolute;
  top: 0;
  border: 0.75rem solid transparent;
  border-bottom: 0.75rem solid #4d4c4d;
  -webkit-transform-origin: center;
          transform-origin: center;
  box-shadow: 2px 2px 4px rgba(0,0,0,0.1);
  z-index: -1;
}
.container .nav:before {
  left: 0;
  -webkit-transform: translateY(-0.45rem) rotate(135deg) translateX(-0.4rem);
          transform: translateY(-0.45rem) rotate(135deg) translateX(-0.4rem);
}
.container .nav:after {
  right: 0;
  -webkit-transform: translateY(-0.45rem) rotate(-135deg) translateX(0.4rem);
          transform: translateY(-0.45rem) rotate(-135deg) translateX(0.4rem);
}
.container .nav a {
  display: inline-block;
  margin: 0 2rem;
  font-size: 2.3rem;
  color: #204c90;
  -webkit-transition: 0.25s;
  transition: 0.25s;
}
.container .nav a:hover {
  opacity: 1;
}

