.input-group {
  position: relative;
}
.input-group .search {
  height: 27px ;
  width: 27px;
  background-color: #204c90;
  border: 0;
  border-radius: 60%;
  cursor: pointer;
  color: #204c90;
  outline: none;
  padding: 15px;
  bottom: 15px;
  position: relative;
  -webkit-transition: all 450ms cubic-bezier(0.2, 0.6, 0.4, 1.2);
  transition: all 450ms cubic-bezier(0.2, 0.6, 0.4, 1.2);
  top: 1px;
  margin-top: -10px !important; 
}
.input-group .search ~ .bar {
  height: 4px;
  width: 15px;
  background: #204c90;
  border-radius: 6px;
  position: absolute;
  display: block;
  opacity: 1;
  right: -9px;
  
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  -webkit-transition: opacity 150ms ease;
  transition: opacity 150ms ease;
}
.input-group .search::-webkit-input-placeholder {
  color: #204c90;
}
.input-group .search:-ms-input-placeholder {
  color: #204c90;
}
.input-group .search::-ms-input-placeholder {
  color: #204c90;
}
.input-group .search::-moz-placeholder {
  color: #204c90;
}
.input-group .search::placeholder {
  color: #204c90;
}
.input-group .search:focus {
  height: 25px;
  width: 230px;
  border-radius: 3px;
  color: #fff;
  cursor: auto;
  cursor: initial;
}
.input-group .search:focus::-webkit-input-placeholder {
  font-weight: bold;
  color: #b8bac5;
}
.input-group .search:focus:-ms-input-placeholder {
  font-weight: bold;
  color: #b8bac5;
}
.input-group .search:focus::-ms-input-placeholder {
  font-weight: bold;
  color: #b8bac5;
}
.input-group .search:focus::-moz-placeholder {
  font-weight: bold;
  color: #b8bac5;
}
.input-group .search:focus::placeholder {
  font-weight: bold;
  color: #b8bac5;
}
.input-group .search:focus ~ .bar {
  opacity: 1;
  right: 0;
  top: 5px;
}

