.tooltip {
  position: relative;
}

.tooltip .tooltiptext {
  visibility: hidden;
  font-size: 19px;
  height: auto;
  width: 300px;
  background-color: rgba(255, 255, 255, 0.856);
  color: #000;
  text-align: center;
  border-radius: 6px;
  border-style: dashed;
  border-color: #e2771f;
  padding: 5px;
  position: absolute;
  z-index: 1;
  bottom: 20%;
  left: 150px;
  margin-left: -60px;
  overflow: hidden;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
}
